.wrapper {
  position: fixed;
  padding: 1rem;
  box-shadow: 0px 0px 0px 3px #f5f4f2;
  border-radius: 2rem;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  z-index: 100;
}
.wrapper.large {
  padding: 1.5rem;
}
.wrapper .header {
  margin-left: 1rem;
  display: flex;
  align-items: center;
  min-height: 35px;
}
.wrapper .header .action {
  margin-left: auto;
}
.wrapper .content {
  padding: 1rem;
  border-radius: 1rem;
  background-color: #fafaf9;
}
.wrapper .content .header {
  color: #505f6b;
  margin-left: 0;
  margin-bottom: 1rem;
}
.wrapper .content .header .action {
  color: #505f6b;
}
.wrapper .content .header .icon {
  color: #505f6b;
  margin-right: 0.375rem;
}
.wrapper .header:not(:has(> *)) {
  display: none;
}

.overflowArrow {
  position: absolute;
  left: 50%;
  translate: -50%;
  bottom: 1rem;
  background-color: #303a4d;
}
.overflowArrow:hover svg {
  color: #303a4d;
}