.desktopPaginationWrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap-reverse;
  gap: 1rem;
}

.pageSizeSelector {
  display: flex;
  align-items: center;
}
.pageSizeSelector .showLabel,
.pageSizeSelector .itemsTotalLabel {
  color: #303a4d;
}
.pageSizeSelector .showLabel {
  margin-right: 0.25rem;
}
.pageSizeSelector .itemsTotalLabel {
  margin-left: 0.75rem;
}

.desktopPagination {
  user-select: none;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.desktopPagination .arrowIcon {
  display: flex;
  align-items: center;
  border-radius: 50%;
  cursor: pointer;
}
.desktopPagination .arrowIcon:hover {
  background-color: rgba(0, 0, 0, 0.04);
}
.desktopPagination .pageNumber {
  position: relative;
  cursor: pointer;
  border-radius: 50%;
  width: 1.5rem;
  height: 1.5rem;
}
.desktopPagination .pageNumber span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.desktopPagination .pageNumber.current {
  color: #fbfbfc;
  background-color: #303a4d;
}
.desktopPagination .pageNumber.current:hover {
  color: #fbfbfc;
  background-color: #303a4d;
}
.desktopPagination .pageNumber.asEllipsis {
  pointer-events: none;
  color: #505f6b;
}
.desktopPagination .pageNumber:hover {
  background-color: rgba(0, 0, 0, 0.04);
}

.mobilePagination {
  padding-block: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.mobilePagination .iconButtons {
  display: flex;
  gap: 0.75rem;
}
.mobilePagination .infoBlock {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  text-align: center;
}
.mobilePagination .currentPage {
  color: #303a4d;
}
.mobilePagination .itemsTotal {
  color: #5d6a75;
}
.mobilePagination .hide {
  visibility: hidden;
}