.dropdownContainer {
  position: relative;
  width: max-content;
}
.dropdownContainer i {
  display: flex;
}

.dropdownSelect {
  width: max-content;
  border-radius: 6.25rem;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.dropdownSelect:hover, .dropdownSelect.selected {
  background-color: rgba(0, 0, 0, 0.04);
}
.dropdownSelect.small {
  padding: 0.5rem 0.75rem 0.5rem 1.25rem;
}
.dropdownSelect.small.withLeftIcon {
  padding-left: 0.75rem;
}
.dropdownSelect.small.fullRounded {
  border-radius: 50%;
  padding: 0.3125rem 0.3125rem 0.25rem 0.3125rem;
}
.dropdownSelect.medium {
  padding: 0.625rem 1rem 0.625rem 1.5rem;
}
.dropdownSelect.medium.withLeftIcon {
  padding-left: 1rem;
}
.dropdownSelect.medium.fullRounded {
  border-radius: 50%;
  padding: 0.375rem 0.375rem 0.3125rem 0.375rem;
}

.label {
  color: #303a4d;
}

.label.medium {
  position: relative;
  top: 0.03125rem;
}

.arrowIcon {
  margin-left: 0.25rem;
  color: #303a4d;
}
.arrowIcon.selected {
  transform: rotate(180deg);
}

.leftIcon {
  color: #303a4d;
}
.leftIcon.small {
  margin-right: 0.375rem;
}
.leftIcon.medium {
  margin-right: 0.5rem;
}

.dropdown {
  width: max-content;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  z-index: 30;
  padding: 1rem;
  background-color: #ffffff;
  border-radius: 1rem;
  box-shadow: 0px 0px 0px 3px #f5f4f2;
}
.dropdown.up {
  bottom: calc(100% + 0.5rem);
}
.dropdown.down {
  margin-top: 0.5rem;
}
.dropdown.stuckToRight {
  left: calc(100% - 3px);
  transform: translateX(-100%);
}

.item {
  display: flex;
  align-items: center;
  border-radius: 0.75rem;
  cursor: pointer;
  color: #303a4d;
}
.item:hover {
  background-color: rgba(0, 0, 0, 0.04);
}
.item.selected {
  background-color: #564fd0;
  color: #fbfbfc;
}
.item.selected .itemIcon {
  color: #fbfbfc;
}
.item.selected:hover {
  background-color: #564fd0;
}
.item.withoutIcons {
  justify-content: center;
}
.item.small {
  padding: 0.5rem 1rem;
}
.item.small.withLeftIcon {
  gap: 0.25rem;
  padding-left: 0.625rem;
}
.item.small.withRightIcon {
  gap: 0.25rem;
  padding-right: 0.625rem;
}
.item.medium {
  padding: 0.5rem 1.25rem;
}
.item.medium.withLeftIcon {
  gap: 0.5rem;
  padding-left: 0.75rem;
}
.item.medium.withRightIcon {
  gap: 0.5rem;
  padding-right: 0.75rem;
}