.container {
  display: flex;
  position: relative;
}
.container.large {
  height: 10.9375rem;
  width: 8.75rem;
}
.container.large:has(.thumbnailContainer:nth-child(n+2)) {
  width: 10.25rem;
}
.container.large:has(.thumbnailContainer:nth-child(n+3)) {
  width: 11.75rem;
}
.container.medium {
  height: 5.25rem;
  width: 4.125rem;
}
.container.medium:has(.thumbnailContainer:nth-child(n+2)) {
  width: 5.125rem;
}
.container.medium:has(.thumbnailContainer:nth-child(n+3)) {
  width: 6.125rem;
}
.container.small {
  height: 4.25rem;
  width: 3.375rem;
}
.container.small:has(.thumbnailContainer:nth-child(n+2)) {
  width: 4.125rem;
}
.container.small:has(.thumbnailContainer:nth-child(n+3)) {
  width: 4.875rem;
}
.container.extraSmall {
  height: 2.5rem;
  width: 2rem;
}
.container.extraSmall:has(.thumbnailContainer:nth-child(n+2)) {
  width: 2.5rem;
}
.container.extraSmall:has(.thumbnailContainer:nth-child(n+3)) {
  width: 3rem;
}

.thumbnailContainer {
  display: flex;
  align-items: center;
  position: absolute;
}
.thumbnailContainer.large {
  width: 8.75rem;
  height: 10.9375rem;
}
.thumbnailContainer.large:nth-of-type(2) {
  left: 1.5rem;
}
.thumbnailContainer.large:nth-of-type(3) {
  left: 3rem;
}
.thumbnailContainer.medium {
  width: 4.125rem;
  height: 5.25rem;
}
.thumbnailContainer.medium:nth-of-type(2) {
  left: 1rem;
}
.thumbnailContainer.medium:nth-of-type(3) {
  left: 2rem;
}
.thumbnailContainer.small {
  width: 3.375rem;
  height: 4.25rem;
}
.thumbnailContainer.small:nth-of-type(2) {
  left: 0.75rem;
}
.thumbnailContainer.small:nth-of-type(3) {
  left: 1.5rem;
}
.thumbnailContainer.extraSmall {
  width: 2rem;
  height: 2.5rem;
}
.thumbnailContainer.extraSmall:nth-of-type(2) {
  left: 0.5rem;
}
.thumbnailContainer.extraSmall:nth-of-type(3) {
  left: 1rem;
}

.thumbnail {
  max-width: 100%;
  max-height: 100%;
  border: 1px solid #f5f4f2;
}
.thumbnail.large {
  border-width: 2px;
}

.placeholder {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #cccfd1;
}
.placeholder.large {
  width: 8.75rem;
  height: 10.9375rem;
}
.placeholder.medium {
  width: 4.125rem;
  height: 5.25rem;
}
.placeholder.small {
  width: 3.375rem;
  height: 4.25rem;
}
.placeholder.extraSmall {
  width: 2rem;
  height: 2.5rem;
}

.count {
  position: absolute;
  top: 50%;
  transform: translate(50%, -50%);
}

.tagContainer {
  position: absolute;
  left: 0.1875rem;
  bottom: 0.25rem;
}