.searchInput {
  position: relative;
  height: 2.5rem;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.icon {
  position: absolute;
  left: 0.5rem;
  top: 0.5rem;
  pointer-events: none;
}

.background {
  position: absolute;
  opacity: 0;
  top: 0;
  left: 0;
  width: 2.5rem;
  height: 2.5rem;
  background-color: rgba(0, 0, 0, 0.04);
  border-radius: 1.25rem;
  transition: all 200ms ease-out;
  cursor: pointer;
}
.background:hover {
  opacity: 1;
}
.alwaysOpen .background, .background:has(~ .input:focus), .background:has(~ .input:not(:placeholder-shown)) {
  opacity: 1;
  width: 100%;
  cursor: auto;
}
.background:has(~ .input:not(:placeholder-shown)) {
  pointer-events: none;
}

.input {
  position: absolute;
  top: 0.625rem;
  left: 2.5rem;
  border: none;
  background: none;
  width: 0;
  color: #303a4d;
}
.input::placeholder {
  color: transparent;
  transition: 0.2s ease all;
}
.alwaysOpen .input, .input:focus:placeholder-shown {
  width: calc(100% - 52px);
}
.alwaysOpen .input::placeholder, .input:focus:placeholder-shown::placeholder {
  color: #5d6a75;
}
.input:focus, .input:not(:placeholder-shown) {
  outline: none;
  width: calc(100% - 118px);
}

.reset {
  opacity: 0;
  color: #5d6a75;
  padding-right: 1.2rem;
  transition: opacity 200ms ease-out;
  pointer-events: none;
}
.reset:has(~ .input:not(:placeholder-shown)) {
  pointer-events: auto;
  opacity: 1;
  cursor: pointer;
}