.container {
  box-sizing: content-box;
  max-width: fit-content;
  padding: 0.1875rem;
}
.container.withFocus:has(:focus-visible) {
  background-color: #e8e7f4;
  border-radius: 1.125rem;
}

.checkboxLabel {
  width: 100%;
  overflow: hidden;
  cursor: pointer;
  display: flex;
  position: relative;
  gap: 1rem;
  color: #303a4d;
}
.checkboxLabel:has(:disabled):not(:has(:checked)) {
  pointer-events: none;
  cursor: default;
}
.checkboxLabel:has(:disabled):not(:has(:checked)) .caption,
.checkboxLabel:has(:disabled):not(:has(:checked)) .label,
.checkboxLabel:has(:disabled):not(:has(:checked)) .labelIcon {
  color: #cccfd1;
}
.checkboxLabel:has(:disabled):not(:has(:checked)) .checkboxFrame {
  border-color: #cccfd1;
}
.checkboxLabel:not(.wrappedInCard):has(:hover) .checkboxFrame {
  border-color: #505f6b;
}
.checkboxLabel:not(.wrappedInCard):has(:hover) .checkboxFrame:has(.switchControl) {
  background-color: #192229;
}
.checkboxLabel:not(.wrappedInCard):has(:hover) .checkboxFrame:has(.switchControl.turnedOn) {
  background-color: #000083;
}
.checkboxLabel:not(.wrappedInCard):has(:focus-visible) .checkboxFocusFrame {
  border-color: #e8e7f4;
}
.checkboxLabel:not(.wrappedInCard):has(:focus-visible) .checkboxFrame {
  border-color: #564fd0;
}
.checkboxLabel:not(.wrappedInCard):has(:disabled) .checkboxFrame {
  border-color: #cccfd1;
}
.checkboxLabel:not(.wrappedInCard):has(:disabled) .checkboxFrame:has(.switchControl) {
  background-color: #cccfd1;
}
.checkboxLabel:not(.wrappedInCard):has(:disabled) .checkboxFrame:has(.switchControl.turnedOn) {
  background-color: #5d6a75;
}
.checkboxLabel:not(.wrappedInCard):has(:disabled) .switchControl.turnedOn {
  background-color: #cccfd1;
}
.checkboxLabel.wrappedInCard {
  flex-direction: column;
  justify-content: space-between;
  border-radius: 1rem;
  padding: 1rem;
  background-color: #ffffff;
  border: 1px solid transparent;
}
.checkboxLabel.wrappedInCard:hover {
  background-color: rgba(0, 0, 0, 0.02);
}
.checkboxLabel.wrappedInCard:has(:focus-visible) {
  border: 1px solid #564fd0;
}
.checkboxLabel.wrappedInCard:has(:checked) {
  background-color: #e8e7f4;
}
.checkboxLabel.wrappedInCard:has(:disabled) {
  background-color: #ffffff;
}

.row {
  display: flex;
  gap: 0.75rem;
  justify-content: space-between;
}
.row.reverse {
  flex-direction: row-reverse;
}
.row.biggerGap {
  gap: 1.5rem;
}
.row.centerItems {
  align-items: center;
}

.labelIconAndLabel {
  min-height: 24px;
  display: flex;
  gap: 0.5rem;
  align-items: center;
}
.labelIconAndLabel .label {
  position: relative;
}

.caption {
  margin-top: 0.5rem;
  color: #505f6b;
}

.checkboxInput {
  display: flex;
  position: relative;
}
.checkboxInput:has(:disabled) svg path:nth-child(1),
.checkboxInput:has(:disabled) svg circle {
  fill: #cccfd1;
}
.checkboxInput:has(:disabled) svg rect {
  stroke: #cccfd1;
}
.checkboxInput .checkboxFocusFrame {
  height: 24px;
  width: 24px;
  border: 2px solid transparent;
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.checkboxInput .checkboxFocusFrame.rounded {
  border-radius: 6.25rem;
}
.checkboxInput .checkboxFocusFrame:has(.switchControl) {
  border-radius: 6.25rem;
  width: 40px;
  height: 24px;
}
.checkboxInput .checkboxFrame {
  height: 20px;
  width: 20px;
  border-radius: 0.375rem;
  border: 2px solid #5d6a75;
}
.checkboxInput .checkboxFrame.rounded {
  border-radius: 6.25rem;
}
.checkboxInput .checkboxFrame:has(.switchControl) {
  border-radius: 6.25rem;
  background-color: #303a4d;
  width: 37px;
  height: 21px;
  border: none;
  display: flex;
  align-items: center;
  padding-left: 0.125rem;
  transition: background-color 200ms ease-out;
}
.checkboxInput .checkboxFrame:has(.switchControl):has(.switchControl.turnedOn) {
  background-color: #564fd0;
}
.checkboxInput .checkboxFrame .switchControl {
  height: 16px;
  width: 16px;
  background-color: #ffffff;
  border-radius: 6.25rem;
  transition: background-color 200ms ease-out, transform 200ms ease-out;
}
.checkboxInput .checkboxFrame .switchControl.turnedOn {
  background-color: #e8e7f4;
  transform: translateX(16px);
}
.checkboxInput .invisibleInput {
  position: absolute;
  opacity: 0;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}
.checkboxInput .invisibleInput:hover {
  cursor: pointer;
}