.dot {
  height: 8px;
  width: 8px;
  border-radius: 100%;
  background-color: #d83542;
}

.number {
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.number.cart {
  min-width: 16px;
  min-height: 16px;
  padding: 0.125rem;
  font-size: 10px;
  line-height: normal;
  background-color: #303a4d;
  color: #fbfbfc;
  border-radius: 6.25rem;
}
.number.bookBadge {
  height: 28px;
  width: 28px;
  background-color: #ffffff;
  box-shadow: 0 0 0 3px #f5f4f2;
  color: #505f6b;
  border-radius: 100%;
}

.text {
  text-transform: uppercase;
  padding: 0.125rem 0.75rem;
  border: 1px solid transparent;
  border-radius: 62.5rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: fit-content;
}
.text:has(.leftIcon) {
  padding-left: 0.5rem;
  padding-right: 0.75rem;
  display: flex;
  align-items: center;
  gap: 0.25rem;
}
.text.info {
  color: #564fd0;
  background-color: #e8e7f4;
}
.text.success {
  color: #0d755a;
  background-color: #def7f0;
}
.text.error {
  color: #ba2e3d;
  background-color: #ffe1e1;
}
.text.neutral {
  color: #505f6b;
  background-color: #edeeef;
}
.text.muted {
  color: #5d6a75;
  background-color: #f5f5f5;
}
.text.outlined {
  color: #5d6a75;
  border-color: #5d6a75;
}
.text.dark {
  color: #ffffff;
  background-color: #192229;
}