.wrapper:has(input:disabled) .label {
  color: #cccfd1;
}
.wrapper:has(input:disabled) .label .optional {
  color: #cccfd1;
}
.wrapper:has(input:disabled) .description {
  color: #cccfd1;
}

.label {
  color: #303a4d;
  margin-bottom: 0.5rem;
  position: relative;
}
.label .optional {
  color: #5d6a75;
}
.label .tooltipIcon {
  margin-left: 0.25rem;
  position: absolute;
  top: 50%;
  translate: 0 -50%;
}

.inputContainer {
  display: inline;
  position: relative;
}
.inputContainer svg,
.inputContainer span {
  position: absolute;
  left: 0.75rem;
  top: 50%;
  translate: 0 -50%;
  pointer-events: none;
}
.inputContainer:has(.small) svg,
.inputContainer:has(.small) span {
  translate: 0 calc(-50% + 1px);
  left: 0.5rem;
}
.inputContainer .passwordToggle {
  position: absolute;
  right: 0.75rem;
  top: 50%;
  translate: 0 -50%;
  color: #564fd0;
}
.inputContainer .passwordToggle:hover {
  text-decoration: underline;
}
.inputContainer .passwordToggle:active {
  color: #3b3e9c;
}

.input {
  padding: 0.6875rem 0.75rem;
  border: 1px solid transparent;
  border-radius: 0.5rem;
  outline: none;
  color: #303a4d;
  width: 100%;
}
.input.filled {
  background-color: #ffffff;
}
.input.outline {
  border-color: #5d6a75;
}
.input.small {
  padding-top: 0.4375rem;
  padding-bottom: 0.4375rem;
}
.input:has(~ svg, ~ span) {
  padding-left: 2.75rem;
}
.input:has(~ svg, ~ span).small {
  padding-left: 2.5rem;
}
.input.hasPasswordToggle {
  padding-right: 5rem;
}
.input::placeholder,
.input ~ svg,
.input ~ span {
  color: #6d7880;
}
.input:hover:not(:disabled, .errorInput, :focus).outline {
  border-color: #505f6b;
}
.input:hover:not(:disabled, .errorInput, :focus)::placeholder,
.input:hover:not(:disabled, .errorInput, :focus) ~ svg,
.input:hover:not(:disabled, .errorInput, :focus) ~ span {
  color: #505f6b;
}
.input:focus {
  border-color: #564fd0;
  box-shadow: 0 0 0 2px #e8e7f4;
}
.input:focus::placeholder,
.input:focus ~ svg,
.input:focus ~ span {
  color: #505f6b;
}
.input.errorInput {
  border-color: #d83542;
  box-shadow: 0 0 0 2px #ffe1e1;
}
.input:disabled {
  color: #cccfd1;
  background-color: #ffffff;
}
.input:disabled.outline {
  border-color: #cccfd1;
}
.input:disabled::placeholder,
.input:disabled ~ svg,
.input:disabled ~ span {
  color: #cccfd1;
}

.description {
  color: #505f6b;
  margin-top: 0.5rem;
}

.error {
  margin-top: 0.5rem;
  display: flex;
  gap: 0.5rem;
  color: #c93140;
}