.container {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.04);
  width: 75px;
  height: 24px;
  border-radius: 6.25rem;
  user-select: none;
}
.container input {
  color: #303a4d;
  padding-top: 0.125rem;
  text-align: center;
  background-color: transparent;
  border: none;
  outline: none;
  -moz-appearance: textfield;
  appearance: textfield;
  pointer-events: none;
}
.container input::-webkit-outer-spin-button, .container input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.container input.editable {
  pointer-events: unset;
}
.container svg {
  color: #303a4d;
  cursor: pointer;
}
.container .disabled {
  color: #cccfd1;
}