.wrapper:has(input:disabled) .label {
  color: #cccfd1;
}
.wrapper:has(input:disabled) .label .optional {
  color: #cccfd1;
}
.wrapper:has(input:disabled) .description {
  color: #cccfd1;
}

.label {
  color: #303a4d;
  margin-bottom: 0.5rem;
  position: relative;
}
.label .optional {
  color: #5d6a75;
}
.label .tooltipIcon {
  margin-left: 0.25rem;
  position: absolute;
  top: 50%;
  translate: 0 -50%;
}

.error {
  margin-top: 0.5rem;
  display: flex;
  gap: 0.5rem;
  color: #c93140;
}