.container {
  border: none;
  background: none;
  /* sizes */
  /* types */
}
.container.small {
  gap: 0.25rem;
  padding: 0.5rem 1rem;
  /* has icon or dot on the left side */
  /* has icon on the right side */
}
.container.small .dot {
  height: 10px;
  width: 10px;
}
.container.small:has(> svg ~ span, > .dot ~ span) {
  padding-left: 0.625rem;
}
.container.small:has(> span + svg) {
  padding-right: 0.625rem;
}
.container.medium {
  gap: 0.5rem;
  padding: 0.625rem 1.25rem;
  /* has icon or dot on the left side */
  /* has icon on the right side */
}
.container.medium .dot {
  height: 14px;
  width: 14px;
}
.container.medium:has(> svg ~ span, > .dot ~ span) {
  padding-left: 0.75rem;
}
.container.medium:has(span + svg) {
  padding-right: 0.75rem;
}
.container.default {
  pointer-events: none;
  border-radius: 0.25rem;
  padding: 0.125rem 0.375rem;
}
.container.default:has(svg) {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  padding-left: 0.25rem;
}
.container.default.neutral-light {
  background-color: #f5f4f2;
  color: #505f6b;
}
.container.default.accent-blue {
  background-color: #e8e7f4;
  color: #564fd0;
}
.container.default.accent-green {
  background-color: #def7f0;
  color: #3d856a;
}
.container.default.accent-magenta {
  background-color: #f3e1f4;
  color: #a41cad;
}
.container.filter, .container.choice {
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  color: #303a4d;
  background-color: rgba(0, 0, 0, 0.04);
  border-radius: 62.5rem;
}
.container.filter:hover, .container.choice:hover {
  color: #192229;
  background-color: rgba(0, 0, 0, 0.1);
}
.container.filter:has(:checked), .container.filter.selected, .container.choice:has(:checked), .container.choice.selected {
  color: #fbfbfc;
  background-color: #564fd0;
}
.container.filter:has(:checked) svg, .container.filter.selected svg, .container.choice:has(:checked) svg, .container.choice.selected svg {
  color: #fbfbfc;
}
.container.filter .dot {
  margin: 3px;
  background-color: rgba(0, 0, 0, 0.06);
  border-radius: 100%;
}