.container {
  display: flex;
  align-items: center;
}
.container.withScaleLegend {
  padding-bottom: 1.25rem;
}

.sliderContainer {
  position: relative;
}

input[type=range] {
  -webkit-appearance: none; /* Hides the slider so that custom slider can be made */
  width: 100%; /* Specific width is required for Firefox. */
  background: transparent; /* Otherwise white in Chrome */
  /* Thumb */
  /* Track */
}
input[type=range]:focus {
  outline: none; /* Removes the blue border. You should probably do some kind of focus styling for accessibility reasons though. */
}
input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: 3px solid #ffffff;
  height: 16px;
  width: 16px;
  border-radius: 6.25rem;
  background: #564fd0;
  cursor: pointer;
  margin-top: -6px; /* You need to specify a margin in Chrome, but in Firefox and IE it is automatic */
  position: relative;
  z-index: 10;
}
input[type=range]::-webkit-slider-thumb:hover {
  border-color: #e8e7f4;
}
input[type=range]::-webkit-slider-thumb:active {
  margin-top: -7px; /* You need to specify a margin in Chrome, but in Firefox and IE it is automatic */
  height: 18px;
  width: 18px;
}
input[type=range]::-moz-range-thumb {
  border: 3px solid #ffffff;
  height: 10px;
  width: 10px;
  border-radius: 6.25rem;
  background: #564fd0;
  cursor: pointer;
}
input[type=range]::-moz-range-thumb:hover {
  border-color: #e8e7f4;
}
input[type=range]::-moz-range-thumb:active {
  height: 12px;
  width: 12px;
}
input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 4px;
  cursor: pointer;
  background: #cccfd1;
  border-radius: 6.25rem;
}
input[type=range]::-moz-range-track {
  width: 100%;
  height: 4px;
  cursor: pointer;
  background: #cccfd1;
  border-radius: 6.25rem;
}

@property --val {
  syntax: "<integer>";
  inherits: true;
  initial-value: 0;
}
output {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #192229;
  color: #fbfbfc;
  height: 26px;
  width: 36px;
  border-radius: 0.5rem;
  transform: translateX(-35%);
  z-index: 10;
}
output:after {
  content: counter(num);
  counter-reset: num var(--val);
}

.scaleMarkers {
  pointer-events: none;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 3px;
  height: 4px;
  display: flex;
  padding: 0 7px;
  justify-content: space-between;
}
.scaleMarkers .marker {
  position: relative;
  width: 4px;
  height: 4px;
  border-radius: 6.25rem;
  background-color: rgba(0, 0, 0, 0.1);
}
@supports (-moz-appearance: none) {
  .scaleMarkers {
    bottom: 0;
    top: 8px;
  }
}

.markerLegend {
  position: absolute;
  top: 12px;
  left: 50%;
  transform: translateX(-50%);
}

.filledBar {
  pointer-events: none;
  position: absolute;
  height: 4px;
  background-color: #564fd0;
  bottom: 3px;
  left: var(--left);
  width: var(--width);
  top: 9px;
}
@supports (-moz-appearance: none) {
  .filledBar {
    bottom: 0;
    top: 8px;
    border-top-left-radius: 100px;
    border-bottom-left-radius: 100px;
  }
  .filledBarRange {
    height: 5px;
    border-radius: unset;
  }
}

.numberInput {
  width: 60px;
  border-radius: 0.5rem;
  margin-left: 1rem;
  padding: 0.75rem;
  text-align: center;
  background-color: #ffffff;
  position: relative;
}
@supports (-moz-appearance: none) {
  .numberInput {
    padding-left: 0.5rem;
  }
}
.numberInput.percentageInput {
  padding-right: 1.375rem;
}
.numberInput.percentageInput::before {
  color: #6d7880;
  position: absolute;
  content: "%";
  top: 50%;
  right: 9px;
  transform: translateY(-50%);
}
.numberInput input {
  text-align: center;
  color: #6d7880;
  outline: none;
  border: none;
  appearance: textfield;
  -moz-appearance: textfield;
}
.numberInput input::-webkit-outer-spin-button, .numberInput input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
@supports (hanging-punctuation: first) and (font: -apple-system-body) and (-webkit-appearance: none) {
  .numberInput input {
    text-align: left;
  }
}

.rangeContainer input:not(:first-of-type) {
  bottom: 3px;
  left: 0;
  right: 0;
  position: absolute;
}
@supports (-moz-appearance: none) {
  .rangeContainer input:not(:first-of-type) {
    bottom: 4px;
  }
}
@supports (-moz-appearance: none) {
  .rangeContainer input {
    pointer-events: none;
    background: none;
  }
  .rangeContainer input::-moz-range-thumb {
    pointer-events: auto;
  }
}