.bottomSheet {
  position: fixed;
  top: 0;
  left: 0;
  background-color: transparent;
  animation: fadeIn 400ms ease forwards;
  height: 100vh;
  width: 100vw;
}
.bottomSheet.isClosing {
  animation: fadeOut 400ms ease forwards;
}

.sheet {
  position: fixed;
  left: 0;
  bottom: -100%;
  background-color: #ffffff;
  border-top-left-radius: 32px;
  border-top-right-radius: 32px;
  width: 100%;
  min-height: 200px;
  max-height: 90%;
  padding: 1.5rem;
  animation: slideIn 400ms ease forwards;
}
.isClosing .sheet {
  animation: slideOut 400ms ease forwards;
}

.layout {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.header {
  display: flex;
  gap: 1.5rem;
  align-items: center;
  min-height: 3rem;
  padding-bottom: 1.5rem;
}

.title {
  flex-grow: 2;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  color: #303a4d;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.closeButton {
  flex-shrink: 0;
}

.contentWrapper {
  flex-grow: 2;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  scrollbar-width: none;
}
.contentWrapper::-webkit-scrollbar {
  display: none;
}

.footer {
  width: 100%;
}

.actionWrapper {
  display: flex;
  padding-top: 1rem;
  gap: 0.5rem;
  justify-content: stretch;
  border-top: 1px solid rgba(0, 0, 0, 0.04);
}

.section {
  display: flex;
  flex-direction: column;
  padding-bottom: 1.5rem;
}

.sectionHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.sectionActionButton {
  all: unset;
  display: inline-block;
  cursor: pointer;
  font-family: "museo-sans";
  font-size: 0.875rem;
  font-style: normal;
  font-weight: 700;
  line-height: 1.125rem;
  color: #505f6b;
}

.sectionTitle {
  color: #303a4d;
}

@keyframes fadeIn {
  0% {
    background-color: transparent;
  }
  100% {
    background-color: rgba(52, 51, 50, 0.7);
  }
}
@keyframes fadeOut {
  0% {
    background-color: rgba(52, 51, 50, 0.7);
  }
  100% {
    background-color: transparent;
  }
}
@keyframes slideIn {
  0% {
    bottom: -100%;
  }
  100% {
    bottom: 0;
  }
}
@keyframes slideOut {
  0% {
    bottom: 0;
  }
  100% {
    bottom: -100%;
  }
}