.backdrop {
  z-index: 9999;
  background-color: rgba(52, 51, 50, 0.7);
  position: fixed;
  height: 100%;
  left: 0;
  top: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-items: center;
  padding: 2rem;
}

.modal {
  margin-inline: auto;
  display: flex;
  flex-direction: column;
  padding: 2rem;
  background-color: #ffffff;
  border-radius: 1.5rem;
}
@media (max-width: 768px) {
  .modal {
    padding: 1.5rem;
  }
}

.header {
  position: relative;
  color: #303a4d;
  margin-bottom: 1.5rem;
}
:global(.brand) .header {
  font-size: 2rem;
}
@media (max-width: 768px) {
  .header {
    margin-bottom: 1rem;
  }
}

.headerInstanceSlot {
  margin-inline: 4.5rem;
}
@media (max-width: 768px) {
  .headerInstanceSlot {
    margin-inline: unset;
  }
}

.title {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-top: 0.125rem;
  margin-right: 4.5rem;
}
.title.alignCenter {
  flex-direction: column;
  justify-content: center;
  margin-top: 2rem;
  margin-right: 0;
  text-align: center;
}
@media (max-width: 768px) {
  .title.alignCenter {
    margin-bottom: 0;
  }
}

.titleIcon {
  min-width: fit-content;
}

.content {
  color: #303a4d;
}
.content.alignCenter {
  text-align: center;
}

.closeBtn {
  position: absolute;
  top: 0;
  right: 0;
}

.footer {
  display: flex;
  align-items: center;
  margin-top: 3rem;
  gap: 0.5rem;
}
@media (max-width: 768px) {
  .footer {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
    margin-top: 2rem;
  }
}

.ctaContainer {
  display: flex;
  gap: 0.5rem;
  margin-left: auto;
}
@media (max-width: 768px) {
  .ctaContainer {
    width: 100%;
    flex-direction: column;
    gap: 1rem;
  }
}

@media (max-width: 768px) {
  .ctaButton {
    width: 100%;
  }
}