.container {
  position: relative;
}

.datePicker {
  color: #303a4d;
  width: 296px;
  padding: 0.5rem;
  background-color: #fafaf9;
}

.datePickerFloatingVariant {
  width: fit-content;
}

.floating {
  box-shadow: 0 0 0 3px #f5f4f2;
  border-radius: 32px;
  background-color: #ffffff;
  position: absolute;
  top: 98px;
  left: 56px;
}
.floating.hidden {
  display: none;
}

.header {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 0;
}
.header .changeMonthButton {
  background-color: transparent;
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  padding: 1rem 0 0.5rem;
}

.footerFloatingVariant {
  gap: 1.5rem;
  align-items: flex-end;
}

.dateGrid {
  display: grid;
  grid-template-columns: repeat(7, 40px);
  grid-auto-rows: 40px;
}

.cellWrapper {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cellWrapper.rangeBoundary::before {
  content: "";
  position: absolute;
  background-color: #e8e7f4;
  width: 20px;
  height: 40px;
}
.cellWrapper.rangeBoundary.rangeStart::before {
  left: 20px;
}
.cellWrapper.rangeBoundary.rangeEnd::before {
  left: 0;
}
.cellWrapper.dataModifierStart {
  border-radius: 1rem 0 0 1rem;
}
.cellWrapper.dataModifierEnd {
  border-radius: 0 1rem 1rem 0;
}

.cell {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.interactiveCell {
  composes: cell;
  position: relative;
  color: #303a4d;
  border-radius: 2.5rem;
  user-select: none;
}
.interactiveCell:hover {
  background-color: rgba(0, 0, 0, 0.04);
  cursor: pointer;
}
.interactiveCell.today {
  color: #564fd0;
}
.interactiveCell.muted {
  color: rgba(0, 0, 0, 0.1);
}
.interactiveCell.disabled {
  color: rgba(0, 0, 0, 0.1);
  pointer-events: none;
}
.interactiveCell.disabledDateModifier:hover {
  cursor: not-allowed;
  background-color: unset;
}
.interactiveCell.selected {
  background-color: #564fd0;
  color: #fbfbfc;
}
.interactiveCell.selected.range {
  background-color: #564fd0;
  color: #fbfbfc;
  border-radius: 2.5rem;
}
.interactiveCell.range {
  background-color: #e8e7f4;
  color: #564fd0;
  border-radius: 0;
}

.weekday {
  composes: cell;
  color: #5d6a75;
}

.monthYearGrid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: 40px;
}

.yearGrid {
  overflow-y: auto;
  height: 160px;
  scrollbar-width: none; /* Firefox */
}
.yearGrid::-webkit-scrollbar {
  display: none; /* Safari and Chrome */
}

.selectButton {
  width: max-content;
  border-radius: 6.25rem;
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 0.625rem 1rem 0.625rem 1.5rem;
}
.selectButton:hover, .selectButton.active {
  background-color: rgba(0, 0, 0, 0.04);
}
.selectButton .label {
  position: relative;
  top: 0.03125rem;
}
.selectButton .arrowIcon {
  margin-left: 0.25rem;
}

.arrowFloatingVariant {
  margin-bottom: 0.625rem;
}