.tablesWrapper {
  position: relative;
}

.table {
  position: relative;
  width: 100%;
  border-collapse: separate;
  border-spacing: 0 0.375rem;
  table-layout: fixed;
}

.swipeActionsTable {
  position: absolute;
  top: 0;
  left: 0;
  width: calc(100% - 1px);
  border-collapse: separate;
  border-spacing: 0 0.375rem;
  table-layout: fixed;
}
.swipeActionsTable.withHeader {
  top: 46px;
}

.rowWrapper {
  position: relative;
  width: 100%;
}

.row {
  position: relative;
  background-color: #fafaf9;
  cursor: pointer;
  transition: background-color 0.2s ease-out;
}
.row:hover {
  background-color: #ffffff;
}
.row:hover .arrowIcon {
  opacity: 1;
  transform: translate(0, -50%);
}
.row.isRowSelected {
  background-color: #ffffff;
}

:global(body.dragging) .row {
  user-select: none;
}

.arrowIcon {
  position: absolute;
  right: -1.5rem;
  top: 50%;
  transform: translate(-0.25rem, -50%);
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.2s ease-out, transform 0.2s ease;
}
.arrowIcon.persists {
  opacity: 1;
}

.verticalPillarIcon {
  position: absolute;
  right: -1.5rem;
  top: 50%;
  transform: translateY(-50%);
  animation: appear 0.2s forwards;
}

@keyframes appear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.header {
  width: max-content;
  color: #5d6a75;
  display: flex;
  align-items: center;
  gap: 0.25rem;
  padding-block: 0.375rem;
  transition: 0.1s;
  padding-right: 1rem;
  margin-bottom: 0.75rem;
}
.header.alignRight {
  margin-left: auto;
}
.header.noMarginBottom {
  margin-bottom: 0;
}
.header i {
  display: flex;
}
.header.canBeSorted {
  position: relative;
  left: -1rem;
  user-select: none;
  padding-left: 1rem;
  cursor: pointer;
}
.header.canBeSorted.alignRight {
  margin-left: auto;
  left: 0.125rem;
}
.header.canBeSorted:hover {
  background-color: rgba(0, 0, 0, 0.04);
  border-radius: 6.25rem;
}
.header.isSorted {
  color: #303a4d;
}

.cell {
  vertical-align: middle;
  padding-right: 1rem;
  color: #303a4d;
}
.cell.alignRight {
  text-align: right;
}
.cell:first-of-type {
  border-top-left-radius: 1rem;
  border-bottom-left-radius: 1rem;
}
.cell:last-of-type {
  border-top-right-radius: 1rem;
  border-bottom-right-radius: 1rem;
  position: relative;
}

.swipeAction {
  display: flex;
  gap: 0.5rem;
  width: 40px;
  margin-left: auto;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: #fbfbfc;
}