.button {
  border: 1px solid transparent;
  border-radius: 6.25rem;
  transition: all 100ms ease-out;
  width: max-content;
}
.button:hover {
  cursor: pointer;
}
.button:disabled {
  cursor: not-allowed;
}
.button .text {
  vertical-align: text-top;
}
.button .text.hidden {
  visibility: hidden;
}
.button .icon {
  vertical-align: middle;
  transition: color 100ms ease-out;
}
.button.large {
  padding: 0.75rem 1.75rem;
}
.button.large.iconLeft {
  padding-left: 1.25rem;
}
.button.large.iconLeft .icon {
  margin-right: 0.625rem;
}
.button.large.iconRight {
  padding-right: 1.25rem;
}
.button.large.iconRight .icon {
  margin-left: 0.625rem;
}
.button.large .icon {
  font-size: 1.5rem;
}
.button.medium {
  padding: 0.625rem 1.5rem;
}
.button.medium.iconLeft {
  padding-left: 1rem;
}
.button.medium.iconLeft .icon {
  margin-right: 0.5rem;
}
.button.medium.iconRight {
  padding-right: 1rem;
}
.button.medium.iconRight .icon {
  margin-left: 0.5rem;
}
.button.medium .icon {
  font-size: 1.25rem;
}
.button.small {
  padding: 0.5rem 1.25rem;
}
.button.small.iconLeft {
  padding-left: 0.75rem;
}
.button.small.iconLeft .icon {
  margin-right: 0.375rem;
}
.button.small.iconRight {
  padding-right: 0.75rem;
}
.button.small.iconRight .icon {
  margin-left: 0.375rem;
}
.button.small .icon {
  font-size: 1rem;
}
.button.primary {
  color: #fbfbfc;
  background-color: #303a4d;
}
.button.primary:hover:not(:disabled) {
  background-color: #445461;
}
.button.primary:hover:not(:disabled):active {
  background-color: #192229;
}
.button.primary:disabled:not(.loading) {
  background-color: #cccfd1;
}
.button.primary .icon {
  color: #fbfbfc;
}
.button.secondary, .button.split {
  color: #303a4d;
  background-color: #edeeef;
}
.button.secondary:hover:not(:disabled), .button.split:hover:not(:disabled) {
  color: #192229;
  background-color: #f5f5f5;
}
.button.secondary:hover:not(:disabled):active, .button.split:hover:not(:disabled):active {
  color: #303a4d;
  background-color: #d8dadb;
}
.button.secondary:hover:not(:disabled) .icon, .button.split:hover:not(:disabled) .icon {
  color: #192229;
}
.button.secondary:disabled:not(.loading), .button.split:disabled:not(.loading) {
  color: #fbfbfc;
  background-color: #cccfd1;
}
.button.secondary:disabled:not(.loading) .icon path, .button.split:disabled:not(.loading) .icon path {
  color: #fbfbfc;
}
.button.secondary .icon, .button.split .icon {
  color: #303a4d;
}
.button.split.large .splitIcon, .button.split.medium .splitIcon, .button.split.small .splitIcon {
  margin-right: 0;
  vertical-align: middle;
  transition: color 100ms ease-out;
  border-left: 1px solid #cccfd1;
  box-sizing: content-box;
}
.button.split.large {
  padding-right: 0.75rem;
  padding-left: 1.75rem;
}
.button.split.large.iconLeft {
  padding-left: 1.25rem;
}
.button.split.large .splitIcon {
  padding-left: 0.625rem;
  margin-left: 0.625rem;
}
.button.split.medium {
  padding-right: 0.625rem;
}
.button.split.medium.iconLeft {
  padding-left: 1rem;
}
.button.split.medium .splitIcon {
  padding-left: 0.5rem;
  margin-left: 0.5rem;
}
.button.split.small {
  padding-right: 0.5rem;
}
.button.split.small.iconLeft {
  padding-left: 0.75rem;
}
.button.split.small .splitIcon {
  padding-left: 0.375rem;
  margin-left: 0.375rem;
}
.button.split:disabled:not(.loading) .splitIcon {
  border-left-color: #fbfbfc;
}
.button.outline {
  color: #303a4d;
  background-color: transparent;
  border-color: #303a4d;
}
.button.outline:hover:not(:disabled) {
  color: #192229;
  background-color: #f5f5f5;
  border-color: #192229;
}
.button.outline:hover:not(:disabled) .icon {
  color: #192229;
}
.button.outline:hover:not(:disabled):active {
  color: #303a4d;
  background-color: #d8dadb;
  border-color: #303a4d;
}
.button.outline:hover:not(:disabled):active .icon {
  color: #303a4d;
}
.button.outline:disabled:not(.loading) {
  color: #cccfd1;
  border-color: #cccfd1;
}
.button.outline:disabled:not(.loading) .icon {
  color: #cccfd1;
}
.button.outline .loading {
  border-color: #303a4d;
}
.button.outline .icon {
  color: #303a4d;
}
.button.destructive {
  color: #fbfbfc;
  background-color: #ba2e3d;
}
.button.destructive:hover:not(:disabled) {
  background-color: #d83542;
}
.button.destructive:hover:not(:disabled):active {
  background-color: #ba2e3d;
}
.button.destructive:disabled:not(.loading) {
  background-color: #ffafb5;
}
.button.destructive .icon {
  color: #fbfbfc;
}
.button.contrast {
  color: #303a4d;
  background-color: #ffffff;
}
.button.contrast:hover:not(:disabled) {
  color: #fbfbfc;
  background-color: #303a4d;
}
.button.contrast:hover:not(:disabled):active {
  background-color: #192229;
}
.button.contrast:hover:not(:disabled) .icon {
  color: #fbfbfc;
}
.button.contrast:disabled:not(.loading) {
  color: #cccfd1;
}
.button.contrast .icon {
  color: #303a4d;
}
.button.ghost {
  color: #303a4d;
  background-color: transparent;
}
.button.ghost:hover:not(:disabled) {
  background-color: #f5f5f5;
}
.button.ghost:hover:not(:disabled):active {
  background-color: #d8dadb;
}
.button.ghost:disabled:not(.loading) {
  color: #cccfd1;
}
.button.ghost:disabled:not(.loading) .icon {
  color: #cccfd1;
}
.button.ghost .icon {
  color: #303a4d;
}

.spinnerContainer {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.spinner {
  animation: spin 1s linear infinite;
}
.spinner.primary path, .spinner.destructive path {
  fill: #fbfbfc;
}
.spinner.secondary path, .spinner.outline path, .spinner.contrast path, .spinner.ghost path, .spinner.split path {
  fill: #303a4d;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
.spinnerAndText {
  display: inline;
  position: relative;
}