.card {
  padding: 1.5rem;
  border-radius: 1.5rem;
  color: #505f6b;
}
.card.action {
  background-color: #f5f4fb;
}
.card.positive {
  background-color: #def7f0;
}
.card.negative {
  background-color: #ffe1e1;
}
.card.neutral {
  background-color: #f5f4f2;
}
.card.info {
  background-color: #fafaf9;
}
.card .header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}
.card .header:has(.ctaBtn) {
  margin-bottom: 1rem;
}
.card .title {
  color: #303a4d;
}
.card .content ~ .ctaBtn {
  margin-top: 1rem;
}
.card .ctaBtn {
  align-self: flex-start;
  min-width: fit-content;
}
@media (max-width: 393px) {
  .card {
    padding: 1rem;
  }
}

.cardWithImage {
  padding: 1rem;
  display: flex;
  border-radius: 1rem;
  overflow: hidden;
  background-color: #fafaf9;
}
.cardWithImage .imageContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 1rem;
}
.cardWithImage .image {
  object-fit: cover;
  height: 64px;
  width: 64px;
  border-radius: 0.5rem;
}
.cardWithImage .placeholder {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #cccfd1;
  height: 64px;
  width: 64px;
  border-radius: 0.5rem;
}
.cardWithImage .content {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  color: #303a4d;
}
.cardWithImage .label {
  color: #5d6a75;
}
.cardWithImage .textLines {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.modularCard {
  padding: 2rem;
}
@media (max-width: 393px) {
  .modularCard {
    padding: 1.5rem;
  }
}
.modularCard.isBoxed {
  border-radius: 1.5rem;
  background-color: #fafaf9;
}
.modularCard.isBoxed .title {
  margin-bottom: 1rem;
}
.modularCard .instanceSlot {
  margin-bottom: 1rem;
}
.modularCard .title {
  color: #303a4d;
  margin-bottom: 1.5rem;
  text-align: center;
}
.modularCard .content {
  color: #303a4d;
  text-align: center;
  padding-inline: 1.5rem;
}
.modularCard .ctaContainer {
  margin-top: 1.5rem;
  display: flex;
  justify-content: center;
  gap: 0.5rem;
}