.notification {
  display: flex;
  flex-direction: column;
  padding: 1.5rem;
  border-radius: 1.5rem;
  gap: 1rem;
}
.notification.positive {
  background-color: #def7f0;
}
.notification.negative {
  background-color: #ffe1e1;
}
.notification.warning {
  background-color: #ffecdb;
}
.notification.neutral {
  background-color: #f0eff6;
}
.notification.error {
  background-color: #d83542;
}
.notification.info {
  background-color: #564fd0;
}
@media (max-width: 393px) {
  .notification {
    padding: 1rem;
  }
}

.row {
  display: flex;
  justify-content: space-between;
  flex-grow: 0;
  gap: 2rem;
  width: 100%;
}
@media (max-width: 393px) {
  .row {
    gap: 1rem;
  }
}

.iconAndContent {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  color: #303a4d;
}
.iconAndContent .icon {
  flex-shrink: 0;
}
.iconAndContent.error, .iconAndContent.info {
  color: #fbfbfc;
}

.ctaAndCloseBtns {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.ctaAndCloseBtns.isCtaBtnAlignedBottom {
  align-items: flex-start;
}

.ctaBtn {
  margin-left: auto;
}
.ctaBtn.noWrap {
  white-space: nowrap;
}